@import "../index.scss";

.button {
  min-width: 100px;
  padding: 10px;
  border-radius: 3px;
  font-size: $fontSizeSml;
  font-family: $fontFamaly;
  font-weight: $fontBold;
  background: none;
  outline: none;
  transition: $transition;
  cursor: pointer;
}

.button:disabled,
.button:disabled:hover {
  color: $colorGrey;
  border-color: $colorGrey;
  cursor: auto;
  background-color: transparent;
}

.dark {
  border: 1px solid $colorWhite;
  color: $colorWhite;
  &:hover {
    background-color: $colorWhite;
    color: $colorBlack;
  }
}

.light {
  border: 1px solid $colorBlack;
  color: $colorBlack;
  &:hover {
    background-color: $colorBlack;
    color: $colorWhite;
  }
}
